<template>
    <section>
       <div class="container">
           <div class="row">
               <div class="col-12">
                   <v-img left
                        class="mx-auto"
                        src="/img/logo-for-splash-screen.png"
                        
                        max-width="30vw"
                        contain
                    ></v-img>
               </div>
               <div class="col-12 light-color text-justify">
                   <p>{{items}}</p>
               </div>
           </div>
       </div>
    </section>
</template>
<script>
  export default {
    props:{
        user:Object
    },
    data () {
      return {
        items:'',
        arabicItems: ' Unic                       .          .',
        englishItems: 'English About',
        persianItems: 'With the unique online currency exchange system, you can automatically exchange your electronic money and digital currencies with the lowest fees, without having to use internal banking systems. Our goal in this set is to provide foreign exchange income for your loved ones',
      }
    },
    methods:{
      checkLanguage(){
          if(this.user.language == 'ar'){
              this.items = this.arabicItems;
          }else if(this.user.language == 'en'){
              this.items = this.englishItems;
          }else{
              this.items = this.persianItems;
          }
      }
    },
    watch:{
        user:{
          handler(){
            this.checkLanguage();
          }
        }
    },
    mounted(){
      this.checkLanguage();
    }
  }
</script>
<style scoped>
    p{
        font-size: 12px
    };
</style>